.investment-calculator {
  text-align: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 300px;
  margin: 0 auto;
  background-color: #f9f9f9;
}

.investment-calculator h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.investment-calculator label {
  display: block;
  margin-bottom: 5px;
}

.investment-calculator input {
  width: 100%;
  padding: 5px;
  margin-bottom: 10px;
}

.investment-calculator button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 15px;
  cursor: pointer;
}

.investment-calculator button:hover {
  background-color: #0056b3;
}

.suggestions {
  margin-top: 20px;
  text-align: left;
}

.suggestions ul {
  list-style-type: disc;
  padding-left: 20px;
}

.suggestions li {
  margin-bottom: 5px;
}
